    function limit_filter(data) {
      console.log(data)
      let duration = data.to - data.from;

      var setDuration = document.getElementById('dur');
      setDuration.innerText = Math.round(duration);

      var startVideo = document.querySelector('#videos').src;
      if (startVideo.indexOf('#')) {
        startVideo = startVideo.split('#')[0];
      }

      let changeStartVideo = startVideo + '#t=' + data.from;
      document.querySelector('#videos').src = changeStartVideo;

      setInterval(function () {
        if (document.querySelector('#videos').currentTime >= data.to) {
          document.querySelector('#videos').pause();
        }
      }, 500);


      if (duration == 0) {
        document.querySelector("#d-pscreen").css('opacity', '1').css('cursor', 'pointer')
        document.querySelector("#d-file").css('opacity', '0.5').css('cursor', 'no-drop')
        document.querySelector("#d-audio").css('opacity', '0.5').css('cursor', 'no-drop')
        document.querySelector("#d-gif").css('opacity', '0.5').css('cursor', 'no-drop')
      } else {
        document.querySelector("#d-pscreen").css('opacity', '0.5').css('cursor', 'no-drop')
        document.querySelector("#d-file").css('opacity', '1').css('cursor', 'pointer')
        document.querySelector("#d-audio").css('opacity', '1').css('cursor', 'pointer')
        document.querySelector("#d-gif").css('opacity', '1').css('cursor', 'pointer')
      }

      if (duration >= 0.1 && duration <= 10) {
        document.querySelector("#d-gif").css('opacity', '1').css('cursor', 'pointer')
      } else {
        document.querySelector("#d-gif").css('opacity', '0.5').css('cursor', 'no-drop')
      }

      let percentage = duration / max_interval * 100
      let color = hsl_col_perc(percentage, 0, 100)


      let leftSlider = document.querySelector('.track-0');
      let centerSlider = document.querySelector('.track-1');
      let rigthSlider = document.querySelector('.track-3');
      const widthTotal = parseInt((leftSlider.style.right), 10);
      const widthOnePercent = widthTotal / 100;

      document.querySelector('.shadow_left').style.width = Math.round(parseInt((centerSlider.style.left), 10) / widthOnePercent) + '%';


      document.querySelector('.shadow_none').style.width = Math.round((widthTotal - parseInt((centerSlider.style.left), 10) - parseInt((centerSlider.style.right), 10)) / widthOnePercent) + '%';

      document.querySelector('.shadow_right').style.width = Math.round(parseInt((centerSlider.style.right), 10) / widthOnePercent) + '%';

      document.querySelector("#ytduration").text(hhmmsss_prettify(duration)).css('color', color)
    }
    function hhmmsss_prettify(n) {
      var sec_num = parseInt(n, 10); // don't forget the second param
      var hours = Math.floor(sec_num / 3600);
      var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
      var seconds = sec_num - (hours * 3600) - (minutes * 60);
      var milisec = n;

      if (hours < 10) { hours = "0" + hours; }
      if (minutes < 10) { minutes = "0" + minutes; }
      if (seconds < 10) { seconds = "0" + seconds; }
      if (milisec < 10) { milisec = "0" + milisec; }
      return hours + ':' + minutes + ':' + seconds + '.' + (milisec % 1).toFixed(1).substring(2);
    }

    function seek(from) {
      if (player.getPlayerState() == 1) {
        player.pauseVideo()
      }
      if (player.getPlayerState() == 2) {
        player.seekTo(from);
      } else {
        // player.pauseVideo();
      }
    }

    function hsl_col_perc(percent, start, end) {
      var a = percent / 100,
        b = (start - end) * a,
        c = b + end;
      // Return a CSS HSL string
      return 'hsl(' + c + ', 100%, 50%)';
    }
